.startpage-header {
	width: 100%;
	height: 100vh;
}

.startpage-header-image {
	width: 50vw;
	border: solid white 10px;
	text-align: right;
	position: absolute;
	top: 50%;
	left: 50%;
	-ms-transform: translateX(-20%) translateY(-50%);
	-webkit-transform: translate(-20%,-50%);
	transform: translate(-20%,-50%);
	background-color: white;
}

.startpage-header-imagedescription {
	background: white;
	margin: 10px;
	font-size: 25px;
}

img {
	width: 100%;
}

.startpage-box {
	background-color: white;
	width: 32vw;
	padding: 15px;
	padding-left: 50px;
	position: absolute;
	top: 50%;
	left: 30%;
	-ms-transform: translateX(-50%) translateY(-50%);
	-webkit-transform: translate(-50%,-50%);
	transform: translate(-50%,-50%);
	box-shadow: #242424BF 3px 3px;
	font-weight: bold;
}

.startpage-box-big {
	font-size: 60px;
	margin-bottom: 0;
}

.startpage-box-small {
	font-size: 40px;
	margin-bottom: 0;
}

@media only screen and (max-width: 600px){
	.startpage-header-image {
		width: 80%;
		border: solid white 10px;
		text-align: right;
		position: absolute;
		top: 40vh;
		left: 27vw;
		background-color: white;
	}

	.startpage-header-imagedescription {
		font-size: 20px;
	}

	.startpage-box {
		background-color: white;
		width: 63vw;
		padding: 0px;
		padding-left: 25px;
		position: absolute;
		top: 60vh;
		left: 39vw;
		box-shadow: #242424BF 3px -3px;
		font-weight: bold;
	}

	.startpage-box-small {
		font-size: 19px;
		margin-bottom: 0;
		margin-top: 15px;
	}

	.startpage-box-big {
		font-size: 35px;
		margin-top: 0;
		margin-bottom: 0;
	}
}